@import url("variables.less");

@media (min-width: @mobile) {
  .main {
    max-width: 320px;
    margin: 0 auto;
  }
}

@media (min-width: @tablet) {
  .main {
    max-width: 768px;
    margin: 0 auto;
  }
}

@media (min-width: @desktop) {
  .main {
    max-width: 1220px;
    margin: 0 auto;
  }
}
